<template>
  <v-card class="card">
    <v-img :src="ItemImage"></v-img>

    <v-card-title primary-title>
      <div>
        <h3 class="headline mb-0">{{ItemName}}</h3>
        <div>{{ ItemDesc }}</div>
      </div>
    </v-card-title>
    <v-card-actions>
      <v-layout row wrap>
        <v-flex xs8>
          <v-btn flat color="blue" @click="toHome">Home</v-btn>
        </v-flex>
        <v-flex xs4>
          <v-layout row wrap>
            <v-flex xs3 offset-xs3>
              <v-icon color="blue" @click="toTwitter">fab fa-twitter</v-icon>
            </v-flex>
            <v-flex xs3>
              <v-icon color="blue" @click="toFacebook">fab fa-facebook</v-icon>
            </v-flex>
            <v-flex xs3>
              <v-icon color="blue" @click="toMail">far fa-envelope</v-icon>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  props: {
    ItemImage: String,
    ItemName: String,
    ItemHome: String,
    ItemFacebook: String,
    ItemTwitter: String,
    ItemDesc: String,
    ItemMail: String
  },
  methods: {
    toHome() {
      window.open(this.ItemHome);
    },
    toFacebook() {
      window.open(this.ItemFacebook);
    },
    toTwitter() {
      window.open(this.ItemTwitter);
    },
    toMail() {
      window.open(`mailto:${this.ItemMail}`);
    }
  }
};
</script>

<style lang="stylus" scoped>
.card {
  width: 95%;
  background-color #f9f9f9;
}
